<script>
import {FwbButton} from "flowbite-vue";
import {useToast} from "vue-toastification";

export default {
  name: "Login",
  components: {FwbButton},
  data(){
    return {
      detum: {
        username: null,
        password: null,
      },
      login: true,
      forget: false,
      regester: false,
      changePassword: false,
      password1: null,
      password2: null,
      passErrors:null,
      errors: {
        new_password1: [],
        new_password2: [],
      },
      regesterData:{
        username: null,
        email: null,
        password1:null,
        password2:null,
      },
      regestererrors:{
        username: null,
        email: null,
        password1:null,
        password2:null,
      },
    }
  },
  methods:{
    Login(){
      console.log(this.$router)
      const params = {
        'username': this.detum.username,
        'password': this.detum.password,
      }
      this.axios.post('/Login/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        if (response.status==200){
            const toast = useToast();
            toast.success("    ");
            this.$router.replace({ path: '/'}).then(()=>{
              this.$nextTick(() => {
                document.title = 'panel'; // Set the new title here
              });
            });
        }
      }).catch((error) => {
          console.error("Login failed:", error); // Log errors for debugging
          const toast = useToast();
          toast.error("  :      !"); // Show error toast
        });
    },
    confirm(){
      const params = {
        'username': this.detum.username,
      }
      // this.changePassword = true;
      this.axios.get('/password_reset/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])

        if (response.data['message']=='password_reset_confirm'){
          this.changePassword = true;
           const toast = useToast();
           toast.success("   !");
        }
      })
    },
    reset(){
      const params = {
        'username': this.detum.username,
        'new_password1': this.password1,
        'new_password2': this.password2
      }
      this.axios.post('/password_reset/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])
        if (response.data['message']=='password_reset_successful'){
          this.forget=false;
          this.changePassword=false
           const toast = useToast();
           toast.success("    ");
        }
        if (response.data['message']=='password_reset_errors'){
          const toast = useToast();
           toast.error("    !");
           this.passErrors = JSON.parse(response.data['data'])
           console.log(this.passErrors)
          // Map backend errors to form fields
          for (const field in this.passErrors) {
            console.log(field)
            if (this.errors.hasOwnProperty(field)) {
              this.errors[field] = this.passErrors[field].map(e => e.message);
            }
          }
        }
      })

    },
    Signup(){
      const params = {
        'username': this.regesterData.username,
        'password1': this.regesterData.password1,
        'password2': this.regesterData.password2,
        'email': this.regesterData.email,
      }
      // this.changePassword = true;
      this.axios.post('/register/', {params: params}, {params: params}).then((response) => {
        console.log(response)
        console.log(response.data['message'])
        console.log(response.data['data'])
        if (response.data['message']=='register_successful'){
           const toast = useToast();
           toast.success("       .");
           this.$router.replace({ path: '/'}).then(()=>{
              this.$nextTick(() => {
                document.title = 'panel'; // Set the new title here
              });
          });
        }
        if (response.data['message']=='register_errors'){
          const toast = useToast();
           toast.error("     !");
           this.passErrors = JSON.parse(response.data['data'])
           console.log(this.passErrors)
          // Map backend errors to form fields
          for (const field in this.passErrors) {
            console.log(field)
            if (this.regestererrors.hasOwnProperty(field)) {
              this.regestererrors[field] = this.passErrors[field].map(e => e.message);
            }
          }
        }
      })
    }
  },
}
</script>

<template>
  <div>
    <teleport to="body">
      <section class="fixed inset-0 bg-gray-50 dark:bg-gray-900 z-50 overflow-y-auto">
        <div class="flex flex-col items-center justify-center px-6 py-8 mx-auto md:h-screen overflow-y-auto lg:py-0">
            <div class="w-full bg-white rounded-lg shadow dark:border md:mt-0 sm:max-w-md xl:p-0 dark:bg-gray-800 dark:border-gray-700">
              <template v-if="login">
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                         
                    </h1>
                    <form class="space-y-4 md:space-y-6" action="#">
                        <div>
                            <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white"> </label>
                            <input v-model="detum.username" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder=" " required="">
                        </div>
                        <div>
                            <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white"> </label>
                            <input v-model="detum.password" type="password" placeholder="" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                        </div>
                    </form>
                    <button @click="Login" class="w-full text-white bg-blue-600 hover:bg-blue-700 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800"></button>
                    <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                    <div class="flex flex-row gap-2">
<!--                    <h1 @click="regester=true; login=false; forget=false;" class="text-blue-600 font-medium text-sm hover:text-blue-800 hover:cursor-pointer"> </h1>-->
<!--                    <span  class="text-blue-600 font-medium text-sm">/</span>-->
<!--                    <h1 @click="forget=true; regester=false; login=false;" class="text-blue-600 font-medium text-sm hover:text-blue-800 hover:cursor-pointer">  </h1>-->
                    <h1 @click="this.$router.push('/passwordResetComplete');" class="text-blue-600 font-medium text-sm hover:text-blue-800 hover:cursor-pointer">  </h1>
                    </div>
                </div>
              </template>
              <template v-if="forget">
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                        
                    </h1>
                    <div class="space-y-4 md:space-y-6">
                        <div v-if="changePassword" class="space-y-4 md:space-y-6">
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">  </label>
                          <input v-model="password1" type="password" placeholder="" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                          <ul class="text-sm text-gray-900">
                            <li>           .</li>
                            <li>        .</li>
                            <li>          .</li>
                            <li>      .</li>
                          </ul>
                          <div v-if="errors.new_password1" class="invalid-feedback">
                            <p v-for="(error, index) in errors.new_password1" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                          </div>
                          <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">  </label>
                          <input v-model="password2" type="password" placeholder="" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
<!--                          <p class="text-sm text-gray-900">           .</p>-->
                          <div v-if="errors.new_password2" class="invalid-feedback">
                            <p v-for="(error, index) in errors.new_password2" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                          </div>
                          <button @click="reset" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800"></button>
                        </div>
                        <div v-else class="space-y-4 md:space-y-6">
                            <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white"> </label>
                            <input v-model="detum.username" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder=" " required="">
                            <button @click="confirm" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800"></button>
                        </div>
                    </div>
                  <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                    <h1 @click="login=true;forget=false;regester=false;changePassword=false;" class="text-red-600 font-medium text-sm hover:text-red-800 hover:cursor-pointer">   </h1>
                </div>
              </template>
              <template v-if="regester">
                <div class="p-6 space-y-4 md:space-y-6 sm:p-8 overflow-y-auto h-full">
                    <h1 class="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                       
                    </h1>
                    <div class="space-y-4 md:space-y-6">
                      <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white"> </label>
                      <input v-model="regesterData.username" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder=" " required="">
                      <div v-if="regestererrors.username" class="invalid-feedback">
                        <p v-for="(error, index) in regestererrors.username" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                      </div>
                      <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white"></label>
                      <input v-model="regesterData.email" type="email" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" placeholder="" required="">
                      <div v-if="regestererrors.email" class="invalid-feedback">
                        <p v-for="(error, index) in regestererrors.email" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                      </div>
                      <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">  </label>
                      <input v-model="regesterData.password1" type="password" placeholder="" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                      <ul class="text-sm text-gray-900">
                        <li>           .</li>
                        <li>        .</li>
                        <li>          .</li>
                        <li>      .</li>
                      </ul>
                      <div v-if="regestererrors.password1" class="invalid-feedback">
                        <p v-for="(error, index) in regestererrors.password1" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                      </div>
                      <label class="block mb-2 text-sm font-medium text-gray-900 dark:text-white">  </label>
                      <input v-model="regesterData.password2" type="password" placeholder="" class="bg-gray-50 border border-gray-300 text-gray-900 rounded-lg focus:ring-primary-600 focus:border-primary-600 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500" required="">
                      <div v-if="regestererrors.password2" class="invalid-feedback">
                        <p v-for="(error, index) in regestererrors.password2" :key="index" class="text-sm text-red-900">>{{ error }}</p>
                      </div>
                      <button @click="Signup" class="w-full text-white bg-green-600 hover:bg-green-700 focus:ring-4 focus:outline-none focus:ring-green-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-blue-800"> </button>
                    </div>
                    <hr class="h-px my-8 bg-gray-200 border-0 dark:bg-gray-700">
                    <h1 @click="login=true;forget=false;regester=false;changePassword=false;" class="text-red-600 font-medium text-sm hover:text-red-800 hover:cursor-pointer">   </h1>
                </div>
              </template>
            </div>
        </div>
      </section>
    </teleport>
  </div>
</template>
